.container {
  background-color: hsl(248, 10%, 15%);
  color: #a4ffaf;
  padding: 12px 15px;
  font-size: larger;

  & input {
    background-color: hsl(248, 10%, 15%);
    border: none;
    color: white;
    width: 90%;
  }
}

.copy {
  background-color: transparent;
  border: none;
  color: #a4ffaf;
  transition: all 200ms ease-in-out;
  cursor: pointer;

  &:hover {
    color: white;
  }
}

.copyText {
  width: 100%;
  text-align: center;
  display: initial;
  position: absolute;
  left: 50%;
  top: -10%;
  transform: translate(-50%, 0);
  animation: copyPass 1s;
}

.hide {
  width: 100%;
  text-align: center;
  display: initial;
  position: absolute;
  left: 50%;
  top: -10%;
  transform: translate(-50%, 0);
  opacity: 0;
  transition: all 200ms ease;
}

@keyframes copyPass {
  0% {
    opacity: 0;
    top: 10%;
  }

  100% {
    opacity: 1;
  }
}
